:root{--main-bg-color: whitesmoke;--secondary-bg-color: white;--main-color: #1B1D1A;--secondary-color: #7DCC21}body{margin:0;font-family:'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

.navigation{position:-webkit-sticky;position:sticky;top:20px;background:var(--main-color);z-index:2}.navigation nav{max-width:1400px;margin:0 auto;height:100px;display:flex;align-items:center;justify-content:space-between}.navigation nav .logo{display:flex}.navigation nav .logo img{padding:0 15px}.navigation nav .logo a{display:flex;align-items:center}.navigation nav a{text-decoration:none;text-transform:uppercase;color:white;padding-right:20px;font-size:24px;letter-spacing:3px;font-weight:100}.social__wrapper{display:flex;align-items:center}.social__wrapper svg{grid-row:1;width:35px;height:35px}.social__wrapper svg path{fill:#fff}.social__city{padding-right:10px;text-transform:uppercase;color:white;font-size:30px !important;font-weight:900 !important}.social a{height:50px;display:flex;align-items:center}

.restaurant-picker__title{text-align:center;text-transform:uppercase;padding:0 30px 30px;color:#7DCC21;font-size:32px}.restaurant-picker__wrapper{height:100%;margin:auto}@media screen and (min-width: 1000px){.restaurant-picker__wrapper{padding:50px 25px 75px}}.restaurant-picker__wrapper ul{list-style:none;padding:0;margin:0;display:flex;justify-content:space-evenly;flex-wrap:wrap;width:100%;height:100%}@media screen and (min-width: 1000px){.restaurant-picker__wrapper ul{display:grid;grid-template-columns:repeat(3, 400px);grid-gap:20px;gap:20px}}@media screen and (max-width: 999px){.restaurant-picker__wrapper ul li{padding-bottom:20px}}

.restaurant-card__wrapper{display:flex;align-items:center;margin:0 auto;background:white;border-radius:5px;padding:30px 10px}.restaurant-card__info{padding-left:15px;width:100%;text-align:center}.restaurant-card__info svg{height:25px}.restaurant-card__info .card-info__city{text-transform:uppercase;text-decoration:none;font-size:24px;letter-spacing:1px;font-weight:bold;color:var(--main-color);line-height:1}.restaurant-card__info .card-info__phone{font-size:16px;line-height:35px}.restaurant-card__info .card-info__phone a{display:flex;justify-content:center;align-items:center;text-decoration:none;color:var(--main-color);background:var(--secondary-color);padding:10px;border-radius:15px}.restaurant-card__info .card-info__phone a .btn--round{display:flex;justify-content:center;align-items:center;padding-right:10px;padding-top:5px}.restaurant-card__info .card-info__phone a svg path{fill:var(--main-color)}.restaurant-card__info .card-info__address{font-size:14px;line-height:1;color:var(--main-color);padding:0 0 10px}

.restaurant-display{max-width:1400px;margin:0 auto;padding:50px 10px}.restaurant-display .go-back{text-align:right;padding-bottom:20px}.restaurant-display .go-back a{text-decoration:none;text-transform:uppercase;color:#4d4d4d}@media screen and (min-width: 1000px){.menu{display:grid;grid-template-columns:1fr 1fr 1fr 1fr;grid-gap:0 35px;gap:0 35px}}.menu_filters{grid-column:1}@media screen and (max-width: 999px){.menu_filters{display:none}}.menu_dishes{grid-column:2 / span 3}

.dish-group{padding:20px}@media screen and (min-width: 1000px){.dish-group{padding:40px}}.dish-group__container{max-width:1400px;border:3px dotted var(--main-color);margin:0 0 100px}@media screen and (min-width: 1000px){.dish-group__container{border:10px dotted var(--main-color)}}.dish-group__wrapper{display:grid;grid-template-columns:repeat(auto-fit, minmax(280px, 1fr));grid-gap:35px;gap:35px}.dish-group__title{text-transform:uppercase;font-size:32px;color:var(--main-color)}@media (min-width: 767px){.dish-group__title{font-size:48px}}.dish-card{display:grid;grid-template-columns:1fr auto;grid-template-rows:250px 60px 1fr auto;grid-row-gap:15px;row-gap:15px;color:#454839;background-color:var(--secondary-bg-color);padding:20px;border-radius:5px}.dish-card__image{grid-column:1 / span 2;grid-row:1}.dish-card__image img{display:block;margin:auto;width:100%;height:250px;object-fit:contain}.dish-card__title{grid-column:1;grid-row:2;text-transform:uppercase;font-size:24px;color:var(--main-color);display:flex;align-items:flex-end}.dish-card__quantity{grid-column:2;grid-row:2;display:flex;justify-content:flex-end;align-items:flex-end;font-weight:bold;font-size:18px}.dish-card__price{grid-column:2;grid-row:4;color:var(--main-color);font-size:14px}.dish-card__description{grid-column:1 / span 2;grid-row:3;letter-spacing:1px;line-height:1.6;font-size:12px}


.menu-filters{font-size:16px;background:white;padding:5px 5px 5px 40px;border-radius:2px}.menu-filters .category{list-style:none;padding-left:0}.menu-filters .category>li{padding:20px 0 0 0}.menu-filters .subcategory{list-style:inside;padding:0;padding-top:10px}.menu-filters .subcategory button{border:none;background:none;font-size:16px;cursor:pointer}

.main-footer{background-color:var(--main-color);margin-top:auto;padding:20px 35px 40px}.main-footer .social{height:40px;padding-top:20px}.main-footer .social svg{width:30px;height:30px;padding-right:5px}.main-footer .social svg path{fill:#fff}.main-footer .links{padding-top:30px;color:white}.main-footer .links h5{padding-bottom:10px;font-size:1.6rem;text-transform:uppercase}.main-footer .links ul{list-style:none;padding:0;margin:0}.main-footer .links ul li{font-size:1.2rem;text-transform:uppercase;letter-spacing:1px;padding-bottom:5px;font-weight:100}.main-footer .links .pages{padding-top:20px}.main-footer .copy{font-size:12px;text-align:center;color:aliceblue}

h1{color:palevioletred}

h1,h2,h3,h4,h5{margin:0}html,body{height:100vh}.container{max-width:1400px;padding:15px;margin:0 auto}.hero{position:relative;box-sizing:border-box;display:flex;justify-content:center;align-items:center;color:white;position:relative;overflow:hidden;min-height:45vh;z-index:1}.video-bg{position:absolute;top:50%;left:50%;transform:translate(-50%, -50%);z-index:-1;min-height:100%;min-width:100%}.btn{padding:10px 20px;border-radius:5px;cursor:pointer;text-decoration:none}.App{display:flex;flex-direction:column;height:100%;min-height:100vh;background-color:var(--main-bg-color)}.pusher{height:200vh}.blinder{background-color:#f7fbfb;height:25px;position:-webkit-sticky;position:sticky;top:0;z-index:2}

